<template>
	<form class="card auth-card" @submit.prevent="submitHandler">
		<div class="card-content">
			<span class="card-title">Домашняя бухгалтерия</span>
			<div class="input-field">
				<input
				id="email"
				type="text"
				v-model.trim="email"
				:class="{invalid: ($v.email.$dirty && !$v.email.required) || ($v.email.$dirty && !$v.email.email)}"
				>
				<label for="email">Email</label>
				<small
					v-if="$v.email.$dirty && !$v.email.required" 
					class="helper-text invalid">Поле Email не должно быть пустым</small>
				<small
					v-else-if="$v.email.$dirty && !$v.email.email" 
					class="helper-text invalid">Введите корректный Email</small>
			</div>
			<div class="input-field">
				<input
				id="password"
				type="password"
				v-model="password"
				:class="{invalid: ($v.password.$dirty && !$v.password.required) || ($v.password.$dirty && !$v.password.minLength)}"
				>
				<label for="password">Пароль</label>
				<small 
					v-if="$v.password.$dirty && !$v.password.required"
					class="helper-text invalid">Введите пароль</small>
				<small 
					v-else-if="$v.password.$dirty && !$v.password.minLength"
					class="helper-text invalid">Пароль должен быть {{$v.password.$params.minLength.min}} симолов. Сейчас он {{password.length}}</small>
			</div>
		</div>
		<div class="card-action">
			<div>
				<button
				class="btn waves-effect waves-light auth-submit"
				type="submit"
				>
				Войти
				<i class="material-icons right">send</i>
			</button>
		</div>

		<p class="center">
			Нет аккаунта?
			<router-link to="/register">Зарегистрироваться</router-link >
		</p>
	</div>
</form>
</template>

<script>
	import {email, required, minLength} from 'vuelidate/lib/validators' //импорт отдельных валидаторов
	import messages from '@/utils/messages'
	export default {
		data() {
			return{
				email: '',
				password: ''
			}

		},
		mounted() {
			console.log(messages['logout']);
			if(messages[this.$route.query.message]){
				this.$message(messages[this.$route.query.message])//вывод сообщения о выходе пользователя из системы с использованием метода из самодельного плагина
			}
	    },
		validations:{
			email: {//поля соответсвуют названиям моделей, привязанных к полям формы
				email,
				required //названия экспортированных валидаторов
			},
			password: {//поля соответсвуют названиям моделей, привязанных к полям формы
				required,
				minLength: minLength(6)
			}
		},
		methods:{
			async submitHandler(){
				console.log(this.$v.password.$params.minLength.min)
				if(this.$v.$invalid){
					this.$v.$touch()
					return
				}
				const formData = {
					email: this.email,
					password: this.password
				}

				try{
					await this.$store.dispatch('login', formData)
						
					this.$router.push('/');

				}catch(e){

				}
				// console.log(formData)
			}
		}
	}
</script>